import { parse } from ".";
import { mapUrlToRootUrl } from "./utils";

async function crawl(rootUrl, sitemapUrl, sites) {
  if (!sites) {
    // Initial crawl
    try {
      return (await crawl(rootUrl, sitemapUrl, [])).sort((a, b) => a.length - b.length);
    } catch (e) {
      // Atm we are ignoring errors
      console.error(e);
      return [];
    }
  } else {
    try {
      const document = await parse(sitemapUrl); // Check if sitemap is an index of multiple sitemaps

      const sitemapIndex = document.querySelector("sitemapindex");

      if (sitemapIndex) {
        const indexUrls = Array.from(sitemapIndex.children).map(c => c.querySelector("loc").textContent); // Iterate sequentially instead of concurrent as we are in a browser and we should avoid parallel requests here (CloudFlare Bots etc.)

        for (const indexUrl of indexUrls) {
          const useUrl = mapUrlToRootUrl(rootUrl, indexUrl) || indexUrl;
          await crawl(rootUrl, useUrl, sites);
        }
      } // Check for URL set (we finally found an URL!)


      const urlSet = document.querySelector("urlset");

      if (urlSet) {
        const urls = Array.from(urlSet.children).map(c => c.querySelector("loc").textContent); // Not needed atm cause WordPress and plugins force the protocol correctly here
        //.map((url) => mapUrlToRootUrl(rootUrl, url) || url);

        sites.push(...urls);
      }
    } catch (e) {
      console.error("Error occurred during \"crawl('".concat(sitemapUrl, "')\":\n\r Error: ").concat(e));
    }
  }

  return sites;
}

export { crawl };